.about {
    @include desktop {
        background-image: url(../images/about/about_bg-img-desc-1x.png);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 0 236px;

        @include retina {
            background-image: url(../images/about/about_bg-img-desc-2x.png);
        }
    }
}

.about.section {
    @include mobile_max{
    padding-bottom: 40px;
    }
}

.about__subtitle {
    font-size: 18px;
    line-height: 1.17;
    color: #ffa5ba;
    letter-spacing: normal;
    margin-top: 0;
    margin-bottom: 15px;

    @include tablet {
        margin-bottom: 16px;
        font-size: 26px;
        line-height: 1.15;
    }

    @include desktop {
        font-size: 30px;
        line-height: 1.13;
    }
}

.about__title {
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 32px;

    @include tablet {
        margin-bottom: 40px;
        font-size: 48px;
    }

    @include desktop {
        margin-bottom: 60px;
        font-size: 58px;
        letter-spacing: 0.06em;
        line-height: 1.14;
    }
}

.about__wrap {
    @include tablet {
        display: flex;
        padding-left: 12px;
        padding-right: 14px;
    }
}

.about__image {
    width: 100%;
    margin-bottom: 40px;

    @include tablet {
        margin-right: 32px;
        margin-bottom: 0;
        max-width: 336px;
        max-height: 335px;

        @include desktop {
            margin-right: 99px;
            max-width: 501px;
            max-height: 501px;
        }
    }
}

.about__wrap-desc {
    @include tablet_max {
        background-image: url(../images/about/about_bg-img-mob-1x.png);
        background-position: 0 71px;
        background-repeat: no-repeat;
        background-size: contain;

        @include retina {
            background-image: url(../images/about/about_bg-img-mob-2x.png);
        }
    }

    @include desktop {
        padding-top: 90px;
    }
}

.about__text {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    color: $primary-text-color;
    max-width: 220px;
    @media screen and (min-width: 400px){
        max-width: 300px;
        font-size: 16px;
    }

    @include tablet {
        max-width: 334px;
        font-size: 14px;
        line-height: 1.76;
        letter-spacing: 0.04em;
    }

    @include desktop {
        max-width: 463px;
        font-size: 16px;
    }
}

.about__desc {
    color: $secondary-text-color;
    margin-top: 21px;
    margin-bottom: 20px;

    @include tablet {
        margin-bottom: 40px;
        margin-top: 15px;
    }

    @include desktop {
        margin-top: 32px;
        font-size: 14px;
        line-height: 1.94;
    }
}

.about__btn {
    display: flex;
    width: 176px;
    height: 46px;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;
    align-items: center;
    justify-content: center;
    border-radius: 22px;
    margin: 0 0;

    @include tablet {
        font-size: 16px;
    }

    @include desktop {
        line-height: 1.31;
    }
}

.about__btn-icon {
    margin-left: 14px;
}

.advantages.section {
    padding-top: 0;
}

.advantages {
    @include tablet {
        padding-left: 12px;
        padding-right: 189px;
    }

    @include desktop {
        padding-left: -4px;
        padding-right: 96px;
    }
}

.advantages__list {
    flex-direction: column;
    gap: 32px;

    @include tablet {
        flex-direction: row;
        justify-content: flex-start;
        // max-width: 527px;
        gap: 0;
    }
}

.advantages__item {
    width: 100%;

    &::before {
        content: '';
        display: block;
        width: 50px;
        height: 50px;
        background-image: url(../images/about/advantages_img-2-1x.png);
        background-repeat: no-repeat;
        background-size: contain;

        @include retina {
            background-image: url(../images/about/advantages_img-2-2x.png);
        }
    }

    &:first-child {
        &::before {
            content: '';
            display: block;
            width: 33px;
            height: 50px;
            background-image: url(../images/about/advantages_img-1-1x.png);
            background-repeat: no-repeat;
            background-size: contain;

            @include retina {
                background-image: url(../images/about/advantages_img-1-2x.png);
            }
        }
    }

    &:last-child {
        &::before {
            content: '';
            display: block;
            width: 44px;
            height: 50px;
            background-image: url(../images/about/advantages_img-3-1x.png);
            background-repeat: no-repeat;
            background-size: contain;

            @include retina {
                background-image: url(../images/about/advantages_img-3-2x.png);
            }
        }
    }
}

.advantages__amount-wrap {
    width: 98px;
    height: 56px;
    margin-bottom: 4px;
    margin-top: 16px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    @include tablet {
        margin-top: 36px;
        margin-bottom: 13px;
    }

    @include desktop {
        margin-top: 40px;
        margin-bottom: 16px;
    }
}

.advantages__amount {
    font-size: 36px;
    line-height: 1.14;
    margin-top: 0;
    @media screen and (min-width: 400px) {
            font-size: 48px;
        }

    @include tablet {
        // font-size: 48px;
        line-height: 1.15;
    }

    @include desktop {
        font-size: 58px;
        line-height: 1.14;
    }
}

.advantages__text {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.7;
    max-width: 230px;
    @media screen and (min-width: 400px){
        font-size: 14px;
    }

    @include tablet {
        // font-size: 14px;
        line-height: 1.74;
        max-width: 180px;
    }

    @include desktop {
        font-size: 16px;
        line-height: 1.76;
        letter-spacing: 0.02em;
        max-width: 317px;
    }
}